
export interface SvgAnnotatorOptions{
    maxLineWidth?: number;
    // Label
    allowMultipleLabel?: boolean; 
    textSelected?;
    labelClicked?;
    labelDblClicked?;
    labelRightClicked?;
    twoLabelsClicked?;
    connectionRightClicked?;
    connectionDblClicked?;
}


export class SvgAnnotatorDefaultOptions implements SvgAnnotatorOptions {
    originString : string = "";
    selectedText: string = "";
    selectedLabelCategory: number = -1;
    selectedConnectionCategory:number = -1;
    startIndex:number = -1;
    endIndex:number = -1;
    first:number = -1;
    second:number = -1;
    maxLineWidth: number= 80;
    allowMultipleLabel: boolean = true;

    /**
     * textSelected
     * @param startIndex 
     * @param endIndex 
     */
    textSelected (startIndex: number, endIndex: number) {};

    /**
     * Label
     * @param id id
     */
    labelClicked (id: number) {};

    /**
     * Label
     * @param id 
     */
    labelDblClicked (id: number){};
      /**
     * Label
     * @param id id
     * @param x X
     * @param y Y
     */
    labelRightClicked(id: number,x: number,y: number){};

    /**
     * Label
     * @param first id
     * @param second id
     */
    twoLabelsClicked (first:number, second: number) {};

    /**
     * 
     * @param id id
     * @param x X
     * @param y Y
     */
    connectionRightClicked (id: number,x: number,y: number) {};

    /**
     * 
     * @param id 
     * @param x 
     * @param y 
     */
    connectionDblClicked (id: number, x: number, y: number) {};
}